body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0px;
  padding: 0px;

}

.food{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 18px;
}
.app_searchform{
  margin-top:18px ;
  text-align: center;
  margin-left: 25px;
}
.app_input{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
}
.app_submit
{
  padding: 6px 16px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  font-size: 15px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}
.app_submit:hover
{
  padding: 6px 16px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  font-size: 15px;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}
.mapdiv{
  justify-content: center;
  margin-top: 25px;
  display: grid;
  grid-template-columns: 230px 230px;
  align-items: baseline;
}